.card {
    border: 6px rgb(250, 250, 250) solid;
    border-radius: 2px;
    width: 180px;
    height: 180px;
    margin: 1rem;
    position: relative;
    box-shadow: 4px 4px 10px rgb(199, 199, 199);
    transition: all .2s ease-in-out;
}
  
.card > .img-container {
    height: 100%;
    overflow: hidden;
    background:rgb(250, 250, 250);
}
  
.card > .img-container > img {
    height: 100%;
}
  
.card:hover {
    box-shadow: 8px 8px 20px rgb(56, 56, 56);
    transform: scale(1.3);
    cursor: pointer;
    border: 8px rgb(253, 249, 2) solid;
    border-radius: 2px;
}
  
.card:active {
    transform: translate(1px, 1px);
    box-shadow: 8px 8px 20px rgb(56, 56, 56);
    transform: scale(1.3);
    cursor: pointer;
    border: 12px rgb(2, 201, 52) solid;
    border-radius: 2px;
}